//reset
*{
  margin: 0;
  padding: 0;
}

html,body{
  height: 100%;
  background-color: lightgray;
}

//grid-container
.grid-container{
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
}

.row:before, .row:after{
    content: "";
    display: table;
    clear: both;
}

[class*='col-']{
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px;
}

@for $i from 1 through 12 {
  .col-#{$i} { width: (100%/12)*$i; }
}

//styles for intro
@mixin inscriptions{
    font-weight: bold;
    font-family: 'Shrikhand', cursive;
    text-align: center;
    text-shadow: 2px 1px white;
    z-index: -100;
}

.unfaithfulDogs{
  @include inscriptions();
  color: navy;
  font-size: 400%;
  padding-top: 15%;
}

.sentence{
  @include inscriptions();
  color: black;
  font-size: 150%;
}

#title{
  @include inscriptions();
  color: 	lightseagreen;

  h2{
      font-size: 450%;
      padding-top: 15%;
  }

  h6{
      font-size: 200%;
  }
}

//styles for Start of game - option choice
#startOptionsGame{
  display: flex;
  justify-content: space-between;
}

#hello{
  font-family: 'Berkshire Swash', cursive;
  font-size: 150%;
  text-shadow: 2px 1px white;
  border-bottom: 1px solid black;
  text-indent: 3%;
  padding-bottom: 2%;
}
#startOptionsGame{
  padding: 15px 0 25px 0;
  border-bottom: 1px solid black;
}

//main mixin for menu buttons
@mixin btnsMenu{
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
}

//styles for menu buttons
.btnInfo{ @include btnsMenu(); background: linear-gradient(#FFF, #FF8C00);}
.btnLicence{@include btnsMenu(); background: linear-gradient(#FFF, #8A2BE2);}
.btnTutorial{@include btnsMenu(); background: linear-gradient(#FFF, #BDB76B);}
.btnNewGame{@include btnsMenu(); background: linear-gradient(#FFF, green);}

.btnInfo:hover, .btnLicence:hover, .btnTutorial:hover, .btnNewGame:hover{
  color: #8B4513;
}

.btnInfo:active, .btnLicence:active, .btnTutorial:active, .btnNewGame:active{
    animation-name: changeBorder;
    animation-duration: 0.2s;
}

//anim for menu buttons
@keyframes changeBorder { 0%   {border-radius: 15%;} 100% {border-radius: 25px;} }

//styles for description Menu
.descriptionMenu{
  text-indent: 3%;
  padding-top: 2%;
  text-shadow: 2px 1px white;
  font-family: 'Berkshire Swash', cursive;
  font-size: 120%;
}
